import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { User } from '../model/user';
import { environment } from '../environments/environment';
import { UserService } from './user.service';
import { PostResponse } from '../model/post-response';
import { Event } from '../model/event';
import { concat } from 'rxjs/operator/concat';

import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';

@Injectable()
export class AuthService {

  private fireDatabase = firebase.database().ref(environment.serverPath + '/users');
  private firebaseUser: Observable<firebase.User>;
  private authenticated = false;

  constructor(
    private http: HttpClient,
    private userService: UserService,
    private af: AngularFireAuth
  ) {
    this.af.authState.subscribe((auth) => {
      if (auth != null) {
        this.firebaseUser = af.authState;
        this.authenticated = true;
      }
    });
  }

  private static saveUserInfo(af: AngularFireAuth): void {
    AuthService.saveUserID(af.auth.currentUser.uid);
    AuthService.saveUserToken(af.auth.currentUser.getIdToken());
  }

  private static saveUserID(id: string): void {
    localStorage.setItem('USER_ID', id);
  }

  public static getUserId(): string {
    return localStorage.getItem('USER_ID');
  }

  private static saveUserToken(token: Promise<any>): void {
    token.then(value => localStorage.setItem('USER_TOKEN', value));
  }

  public static getUserToken(): string {
    return localStorage.getItem('USER_TOKEN');
  }

  public static isLoggedIn(): boolean {
    if (AuthService.getUserId()) {
      return true;
    }
    return false;
  }

  public getUser(): Observable<User> {
    return this.userService.getUser(AuthService.getUserId());
  }

  public addUserEvent(eventId: string): Observable<PostResponse> {
    return this.http.post<PostResponse>(`${environment.serverUrl}/users/${AuthService.getUserId()}/ownedEventIds.json`,
      `\"${eventId}\"`);
  }

  public signInGG(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.af.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider()).then(res => {
        console.log(this.firebaseUser);
        this.authenticated = true;
        const userInfo: any = res.user;
        // Save data from user into database here
        this.af.auth.currentUser.updateProfile({
          displayName: userInfo.displayName,
          photoURL: userInfo.photoURL
        }).then(() => {
          this.fireDatabase.child(this.af.auth.currentUser.uid).update({
            uid: this.af.auth.currentUser.uid,
            email: userInfo.email,
            displayName: userInfo.displayName,
            photoURL: userInfo.photoURL
          }).then(() => {
            AuthService.saveUserInfo(this.af);
            resolve(true);
          }).catch(err => {
            reject(err);
          });
        }).catch(err => {
            reject(err);
          });
        console.log(res.user.displayName);
        console.log(res.user.email);
      })
      .catch(err => {
        reject(err);
        console.log(err);
      });
    });
  }

  public signInFB(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.af.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider()).then(res => {
        this.authenticated = true;
        const userInfo: any = res.user;
        // Save data from user into database here
        this.af.auth.currentUser.updateProfile({
          displayName: userInfo.displayName,
          photoURL: userInfo.photoURL
        }).then(() => {
          this.fireDatabase.child(this.af.auth.currentUser.uid).update({
            uid: this.af.auth.currentUser.uid,
            email: userInfo.email,
            displayName: userInfo.displayName,
            photoURL: userInfo.photoURL
          }).then(() => {
            AuthService.saveUserInfo(this.af);
            resolve(true);
          }).catch(err => {
            reject(err);
          });
        })
        .catch(err => {
          reject(err);
        });

        console.log(res.user.displayName);
        console.log(res.user.email);
      })
      .catch(err => {
        reject(err);
        console.log(err);
      });
    });
  }

  public signOut(): Promise<any> {
    this.authenticated = false;
    localStorage.removeItem('USER_ID');
    localStorage.removeItem('USER_TOKEN');
    return this.af.auth.signOut();
  }
}



