import { Component, Input, OnInit } from '@angular/core';
import { NavigationService } from '../../services/navigation.service';
import { EventService } from '../../services/event.service';
import { Event } from '../../model/event';
import { EventsListPageComponent } from '../events-list-page/events-list-page.component';

@Component({
  selector: 'app-event-listing',
  templateUrl: './event-listing.component.html',
  styleUrls: ['./event-listing.component.css']
})
export class EventListingComponent {

  @Input() eventId: string;
  @Input() event: Event;

  constructor(private navigationService: NavigationService) { }

  private goToEventPage(): void {
    if (this.eventId) {
      this.navigationService.navigateAndSave(`/page/events/${this.eventId}`);
    }
  }

  private getFormattedDate(): string {
    if (this.event && this.event.date) {
      return EventService.formatDateTimeShort(this.event.date);
    } else {
      return '';
    }
  }

  private getNumberAttending(): number {
    if (this.event && this.event.participantIds) {
      return Object.keys(this.event.participantIds).length;
    } else {
      return 0;
    }
  }
}



