import { Component, OnInit } from '@angular/core';
import { NavigationService } from '../../services/navigation.service';
import { EventService } from '../../services/event.service';
import { Event } from '../../model/event';
import { AuthService } from '../../services/auth.service';
import { Location } from '../../model/location';

@Component({
  selector: 'app-events-list-page',
  templateUrl: './events-list-page.component.html',
  styleUrls: ['./events-list-page.component.css']
})
export class EventsListPageComponent implements OnInit {

  readonly PREDEFINED_EVENT_LOCATIONS = EventService.PREDEFINED_EVENT_LOCATIONS;

  loading = false;

  events: Event[];
  eventIds: string[];

  loadEventsRef: () => void = () => this.loadEvents();

  constructor(private navigationService: NavigationService, private eventService: EventService) { }

  ngOnInit(): void {
    this.navigationService.setTitle(NavigationService.EVENTS_LIST_TITLE);
    this.loadEvents();
  }

  private loadEvents(location?: string): void {
    this.loading = true;
    this.eventService.getAllEvents(location).subscribe(events => {
      if (events) {
        this.eventIds = Object.keys(events);
        this.events = Object.keys(events).map(key => events[key]);
        EventService.formatEventDates(this.events);
      }
      this.loading = false;
    }, error => {
      if (error.status === 401 || error.status === 403) {
        this.navigationService.navigateToRoot();
      }
      this.loading = false;
    });
  }
}



