import { Component, OnInit } from '@angular/core';
import { Event } from '../../model/event';
import { AuthService } from '../../services/auth.service';
import { NavigationService } from '../../services/navigation.service';
import { EventService } from '../../services/event.service';

@Component({
  selector: 'app-user-events-joined-page',
  templateUrl: './user-events-joined-page.component.html',
  styleUrls: ['./user-events-joined-page.component.css']
})
export class UserEventsJoinedPageComponent implements OnInit {

  loading = false;

  events: Event[];
  eventIds: string[];

  constructor(
    private navigationService: NavigationService,
    private authService: AuthService,
    private eventService: EventService
  ) { }

  ngOnInit(): void {
    this.navigationService.setTitle(NavigationService.USER_EVENTS_JOINED_TITLE);
    this.loadUserJoinedEvents();
  }

  private loadUserJoinedEvents(): void {
    this.loading = true;
    this.authService.getUser().subscribe(user => {
      if (user && user.joinedEventIds) {
        this.eventIds = Object.keys(user.joinedEventIds).map(key => user.joinedEventIds[key]);
        this.eventService.getEvents(this.eventIds).subscribe(events => {
          this.loading = false;
          this.events = events;
          EventService.formatEventDates(this.events);
        });
      } else {
        this.loading = false;
      }
    }, error => {
      if (error.status === 401 || error.status === 403) {
        this.navigationService.navigateToRoot();
      }
      this.loading = false;
    });
  }
}



