import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs/Observable';
import { PostResponse } from '../model/post-response';
import { Event } from '../model/event';
import { Location } from '../model/location';
import { AuthService } from './auth.service';
import 'rxjs/add/observable/forkJoin';

@Injectable()
export class EventService {

  public static readonly PREDEFINED_EVENT_LOCATIONS: Location[] = [
    {name: 'Stamps Field', longitude: 33.778321, latitude: -84.403176},
    {name: 'Campus Recreation Center', longitude: 33.775799, latitude: -84.403590},
    {name: 'Tech Green', longitude: 33.774687, latitude: -84.397380},
    {name: 'Burger Bowl (Couch Park)', longitude: 33.774246, latitude: -84.398728},
    {name: 'Fifth Street Plaza', longitude: 33.776912, latitude: -84.390840},
  ];

  private static readonly MONTH_NAMES: string[] = [
    'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'
  ];

  constructor(private http: HttpClient, private authService: AuthService) { }

  public static stringToDate(date: string): Date {
    return new Date(`${date.substring(0, date.length - 5)}.${date.substring(date.length - 4, date.length - 1)}`);
  }

  public static formatDateTime(date: Date): string {
    return `${this.MONTH_NAMES[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()} at `
      + date.toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'});
  }

  public static formatDateTimeShort(date: Date): string {
    return `${date.getMonth() + 1}/${date.getDate()} ${date.toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'})}`;
  }

  public static formatEventDates(events: Event[]): void {
    for (const event of events) {
      if (event) {
        event.date = EventService.stringToDate(event.date.toString());
      }
    }
  }

  public postEvent(event: Event, then?: (value: PostResponse) => void, error?: (error: any) => void): void {
    this.http.post<PostResponse>(`${environment.serverUrl}/events.json`, event).subscribe(value => {
      this.authService.addUserEvent(value.name).subscribe(value2 => {
        then(value);
        }, err => {
        error(err);
      });
    }, err => {
      error(err);
    });
  }

  public getAllEvents(location?: string): Observable<Event[]> {
    let url = `${environment.serverUrl}/events.json`;
    if (location) {
      url += `?orderBy="location/name"&equalTo="${location}"`;
    }
    return this.http.get<Event[]>(url);
  }

  public getEvent(id: string): Observable<Event> {
    return this.http.get<Event>(`${environment.serverUrl}/events/${id}.json`);
  }

  public getEvents(ids: string[]): Observable<Event[]> {
    const events: Observable<Event>[] = ids.map(id => this.getEvent(id));
    return Observable.forkJoin(events);
  }

  public deleteEvent(id: string, userEventKey: string): Observable<any> {
    const userId: string = AuthService.getUserId();

    const eventResponse: Observable<any> = this.http.delete<any>(`${environment.serverUrl}/events/${id}.json`);
    const userResponse: Observable<any>
      = this.http.delete<any>(`${environment.serverUrl}/users/${userId}/ownedEventIds/${userEventKey}.json`);

    return Observable.forkJoin<any>(eventResponse, userResponse);
  }

  public joinEvent(eventId: string): Observable<PostResponse[]> {
    const userId: string = AuthService.getUserId();

    const eventResponse: Observable<PostResponse>
      = this.http.post<PostResponse>(`${environment.serverUrl}/events/${eventId}/participantIds.json`, `\"${userId}\"`);
    const userResponse: Observable<PostResponse>
      = this.http.post<PostResponse>(`${environment.serverUrl}/users/${userId}/joinedEventIds.json`, `\"${eventId}\"`);

    return Observable.forkJoin<PostResponse>(eventResponse, userResponse);
  }

  public leaveEvent(eventId: string, participantKey: string, joinedEventKey: string): Observable<any> {
    const userId: string = AuthService.getUserId();

    let eventResponse: Observable<PostResponse> =  new Observable<PostResponse>();
    let userResponse: Observable<PostResponse> =  new Observable<PostResponse>();

    if (eventId && participantKey) {
      eventResponse = this.http.delete<any>(`${environment.serverUrl}/events/${eventId}/participantIds/${participantKey}.json`);
    }
    if (userId && joinedEventKey) {
      userResponse = this.http.delete<any>(`${environment.serverUrl}/users/${userId}/joinedEventIds/${joinedEventKey}.json`);
    }
    return Observable.forkJoin<any>(eventResponse, userResponse);
  }
}



