import { Component, OnInit } from '@angular/core';
import { EventService } from '../../services/event.service';
import { Event } from '../../model/event';
import { ActivatedRoute, Params } from '@angular/router';
import { User } from '../../model/user';
import { UserService } from '../../services/user.service';
import { AuthService } from '../../services/auth.service';
import { NavigationService } from '../../services/navigation.service';
import { ModalService } from '../../services/modal.service';

@Component({
  selector: 'app-event-page',
  templateUrl: './event-page.component.html',
  styleUrls: ['./event-page.component.css']
})
export class EventPageComponent implements OnInit {

  loading = false;

  event: Event;
  eventId: string;
  owner: User;
  ownerEventKey: string;
  participants: User[];
  participantIds: string[];
  formattedDateTime: string;

  constructor(
    private navigationService: NavigationService,
    private modalService: ModalService,
    private authService: AuthService,
    private eventService: EventService,
    private userService: UserService,
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.navigationService.setTitle(NavigationService.EVENT_TITLE);
    this.activatedRoute.params.subscribe((params: Params) => {
      this.eventId = params['id'];
      this.getEvent();
    });
  }

  private getEvent(): void {
    if (!this.eventId) {
      return;
    }
    this.loading =  true;
    this.eventService.getEvent(this.eventId).subscribe(event => {
      this.loading = false;
      if (event) {
        event.date = EventService.stringToDate(event.date.toString());
        this.event = event;
        this.formattedDateTime = EventService.formatDateTime(event.date);
        if (event.ownerId) {
          this.userService.getUser(event.ownerId).subscribe(owner => {
            this.owner = owner;
            this.setOwnerEventKey(owner);
          });
        }
        if (event.participantIds) {
          this.participantIds = Object.keys(event.participantIds).map(key => event.participantIds[key]);
          this.userService.getUsers(this.participantIds).subscribe(participants => this.participants = participants);
        } else {
          this.participantIds = [];
          this.participants = [];
        }
      }
    }, error => {
      if (error.status === 401 || error.status === 403) {
        this.navigationService.navigateToRoot();
      }
      this.loading = false;
    });
  }

  private setOwnerEventKey(owner: User): void {
    if (owner.ownedEventIds) {
      for (const key of Object.keys(owner.ownedEventIds)) {
        if (owner.ownedEventIds[key] === this.eventId) {
          this.ownerEventKey = key;
        }
      }
    }
  }

  private isOwner(): boolean {
    return this.event && this.event.ownerId === AuthService.getUserId();
  }

  private isParticipant(): boolean {
    return this.participantIds && this.participantIds.includes(AuthService.getUserId());
  }

  private joinEvent(): void {
    this.eventService.joinEvent(this.eventId).subscribe(value => this.getEvent());
  }

  private deleteEvent(): void {
    this.eventService.deleteEvent(this.eventId, this.ownerEventKey).subscribe(value => {
      const message = `Your event, ${this.event.name} has successfully been deleted.`;
      this.modalService.openAlertModal('Successfully Deleted Event', message, () => {
        if (this.navigationService.canReturn()) {
          this.navigationService.return();
        } else {
          this.navigationService.navigate('/page/user/events/owned');
        }
      });
    }, error => {
      this.modalService.openAlertModal('Failed to Delete Event');
    });
  }

  private leaveEvent(): void {
    this.authService.getUser().subscribe(user => {
      const userId: string = AuthService.getUserId();

      let participantKey: string;
      let joinedEventKey: string;

      if (this.event && this.event.participantIds) {
        for (const key of Object.keys(this.event.participantIds)) {
          if (this.event.participantIds[key] === userId) {
            participantKey = key;
          }
        }
      }
      if (user && user.joinedEventIds) {
        for (const key of Object.keys(user.joinedEventIds)) {
          if (user.joinedEventIds[key] === this.eventId) {
            joinedEventKey = key;
          }
        }
      }
      this.eventService.leaveEvent(this.eventId, participantKey, joinedEventKey).subscribe(value => this.getEvent());
    });
  }
}



