import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { MainToolbarComponent } from './main-toolbar/main-toolbar.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  MatButtonModule, MatCardModule, MatCheckboxModule, MatDatepickerModule, MatFormFieldControl, MatFormFieldModule, MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatNativeDateModule, MatProgressSpinnerModule, MatSelectModule,
  MatSidenavModule,
  MatToolbarModule
} from '@angular/material';
import { MainSidenavComponent } from './main-sidenav/main-sidenav.component';
import { RouterModule, Routes } from '@angular/router';
import { LoginPageComponent } from './login-page/login-page.component';
import { NotFoundPageComponent } from './not-found-page/not-found-page.component';
import { EventsListPageComponent } from './events-list-page/events-list-page.component';
import { UserEventsOwnedPageComponent } from './user-events-owned-page/user-events-owned-page.component';
import { UserEventsJoinedPageComponent } from './user-events-joined-page/user-events-joined-page.component';
import { UserProfilePageComponent } from './user-profile-page/user-profile-page.component';
import { EventPageComponent } from './event-page/event-page.component';
import { CreateEventPageComponent } from './create-event-page/create-event-page.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { EventService } from '../services/event.service';
import { HTTP_INTERCEPTORS, HttpClient, HttpClientModule } from '@angular/common/http';
import { ModalAlertContentComponent } from './modal-alert-content/modal-alert-content.component';
import { ModalService } from '../services/modal.service';
import { UserService } from '../services/user.service';
import { AuthService } from '../services/auth.service';
import { NavigationService } from '../services/navigation.service';
import { AngularFireModule } from 'angularfire2';
import { environment } from '../environments/environment';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { CreateEventButtonComponent } from './create-event-button/create-event-button.component';
import { EventListingComponent } from './event-listing/event-listing.component';
import { AuthGuard } from '../guards/auth.guard';
import { TokenInterceptor } from '../interceptors/token.interceptor';
import { AdsenseModule } from 'ng2-adsense';

const routes: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  {path: 'login', component: LoginPageComponent},
  {path: 'page', component: MainToolbarComponent,
    children: [
      {path: '', redirectTo: 'events', pathMatch: 'full'},
      {path: 'events', redirectTo: 'events/list', pathMatch: 'full'},
      {path: 'events/list', component: EventsListPageComponent},
      {path: 'events/create', component: CreateEventPageComponent},
      {path: 'events/:id', component: EventPageComponent},
      {path: 'user/events', redirectTo: 'user/events/owned', pathMatch: 'full'},
      {path: 'user/events/owned', component: UserEventsOwnedPageComponent},
      {path: 'user/events/joined', component: UserEventsJoinedPageComponent},
      {path: 'user/profile', component: UserProfilePageComponent}
    ]},
  {path: '**', component: NotFoundPageComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    NotFoundPageComponent,
    MainToolbarComponent,
    MainSidenavComponent,
    ModalAlertContentComponent,
    LoginPageComponent,
    EventsListPageComponent,
    UserEventsOwnedPageComponent,
    UserEventsJoinedPageComponent,
    UserProfilePageComponent,
    EventPageComponent,
    CreateEventPageComponent,
    CreateEventButtonComponent,
    EventListingComponent
  ],
  entryComponents: [ModalAlertContentComponent],
  imports: [
    RouterModule.forRoot(routes),
    AdsenseModule.forRoot({
      adClient: 'ca-pub-6702912321418369',
      adSlot: 7259870550,
    }),
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    MatCardModule,
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    MatSidenavModule,
    MatListModule,
    MatFormFieldModule,
    MatInputModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatSelectModule,
    MatGridListModule,
    MatCheckboxModule,
    MatProgressSpinnerModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    NgbModule.forRoot(),
  ],
  exports: [RouterModule],
  providers: [
    HttpClient,
    NavigationService,
    ModalService,
    AuthService,
    UserService,
    EventService,
    AuthGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



