import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { EventService } from '../../services/event.service';
import { Event } from '../../model/event';
import { Location } from '../../model/location';
import { ErrorStateMatcher } from '@angular/material';
import { ModalService } from '../../services/modal.service';
import { AuthService } from '../../services/auth.service';
import { User } from '../../model/user';

@Component({
  selector: 'app-create-event-page',
  templateUrl: './create-event-page.component.html',
  styleUrls: ['./create-event-page.component.css']
})
export class CreateEventPageComponent implements OnInit {

  readonly PREDEFINED_EVENT_LOCATIONS = EventService.PREDEFINED_EVENT_LOCATIONS;

  name: string;
  date: FormControl = new FormControl(new Date());
  time: FormControl = new FormControl('12:00');
  description: string;
  location: FormControl = new FormControl('', [Validators.required]);
  alternateEmail: string;

  isInModal: boolean;

  constructor(private authService: AuthService, private eventService: EventService, private modalService: ModalService) { }

  ngOnInit(): void {
    this.isInModal = this.modalService.modelIsOpened();
  }

  onSubmit(): void {
    if (this.name && this.date.valid && this.time.valid && this.location.valid) {
      const dateTime: Date = new Date(this.date.value.getFullYear() + '/' + (this.date.value.getMonth() + 1) + '/'
        + this.date.value.getDate() + ' ' + this.time.value);
      this.eventService.postEvent({name: this.name, date: dateTime, description: this.description, location: this.location.value,
        contactEmail: this.alternateEmail, ownerId: AuthService.getUserId(), participantIds: []},
          value => {
          this.modalService.closeTopModal();
          this.modalService.openAlertModal('Successfully Created Event!', 'Your new event, \"' + this.name
            + '\" was created.');
        }, error => {
          this.modalService.openAlertModal('Failed to Create New Event', 'Your new event, \"' + this.name
            + '\" could not be created.');
        });
    }
  }

  private closeModal(): void {
    this.modalService.closeTopModal();
  }
}



