import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { User } from '../model/user';
import { environment } from '../environments/environment';
import { UserService } from './user.service';
import { PostResponse } from '../model/post-response';
import { Event } from '../model/event';
import { NavigationEnd, Router } from '@angular/router';
import 'rxjs/add/operator/filter';

@Injectable()
export class NavigationService {

  public static readonly DEFAULT_TITLE = 'Popup Games';
  public static readonly EVENTS_LIST_TITLE = 'Discover';
  public static readonly EVENT_TITLE = 'Event Details';
  public static readonly USER_EVENTS_JOINED_TITLE = 'My RSVPs';
  public static readonly USER_EVENTS_OWNED_TITLE = 'My Events';
  public static readonly USER_PROFILE_TITLE = 'My Profile';
  public static readonly CREATE_EVENT_TITLE = 'Create Event';

  public static readonly LANDING_PAGE_ROUTE = '/page/events/list';

  private returnStack: string[] = [];
  private returnStackClearLock = false;

  private title = NavigationService.DEFAULT_TITLE;

  constructor(private router: Router) {
    this.router.events
      .filter((event) => event instanceof NavigationEnd)
      .subscribe((event) => {
        if (!this.returnStackClearLock) {
          this.returnStack = [];
        }
      });
  }

  public getTitle(): string {
    return this.title ? this.title : NavigationService.DEFAULT_TITLE;
  }

  public setTitle(title: string): void {
    setTimeout(() => this.title = title);
  }

  public resetTitle(): void {
    this.setTitle(NavigationService.DEFAULT_TITLE);
  }

  public navigate(route: string): void {
    this.returnStackClearLock = true;
    this.router.navigate([route]).then((value) => this.returnStackClearLock = false);
  }

  public navigateToRoot(): void {
    this.navigate('');
  }

  public navigateToLandingPage(): void {
    this.navigate(NavigationService.LANDING_PAGE_ROUTE);
  }

  public navigateAndSave(route: string): void {
    this.returnStack.push(this.router.url);
    this.navigate(route);
  }

  public canReturn(): boolean {
    return this.returnStack.length > 0;
  }

  public return(): void {
    if (this.canReturn()) {
      this.navigate(this.returnStack.pop());
    }
  }
}



