import { Component, OnInit } from '@angular/core';
import { ModalService } from '../../services/modal.service';
import { CreateEventPageComponent } from '../create-event-page/create-event-page.component';
import { EventService } from '../../services/event.service';
import { Event } from '../../model/event';
import { AuthService } from '../../services/auth.service';
import { NavigationService } from '../../services/navigation.service';

@Component({
  selector: 'app-user-events-owned-page',
  templateUrl: './user-events-owned-page.component.html',
  styleUrls: ['./user-events-owned-page.component.css']
})
export class UserEventsOwnedPageComponent implements OnInit {

  loading = false;

  events: Event[];
  eventIds: string[];

  loadUserOwnedEventsRef: () => void = () => this.loadUserOwnedEvents();

  constructor(
    private navigationService: NavigationService,
    private modalService: ModalService,
    private authService: AuthService,
    private eventService: EventService
  ) { }

  ngOnInit(): void {
    this.navigationService.setTitle(NavigationService.USER_EVENTS_OWNED_TITLE);
    this.loadUserOwnedEvents();
  }

  private loadUserOwnedEvents(): void {
    this.loading = true;
    this.authService.getUser().subscribe(user => {
      if (user && user.ownedEventIds) {
        this.eventIds = Object.keys(user.ownedEventIds).map(key => user.ownedEventIds[key]);
        this.eventService.getEvents(this.eventIds).subscribe(events => {
          this.events = events;
          EventService.formatEventDates(this.events);
          this.loading = false;
        });
      } else {
        this.loading = false;
      }
    }, error => {
      if (error.status === 401 || error.status === 403) {
        this.navigationService.navigateToRoot();
      }
      this.loading = false;
    });
  }

  private goToEventPage(index: number): void {
    this.navigationService.navigateAndSave(`/page/events/${this.eventIds[index]}`);
  }
}



