import { Component, Input, OnInit } from '@angular/core';
import { CreateEventPageComponent } from '../create-event-page/create-event-page.component';
import { ModalService } from '../../services/modal.service';

@Component({
  selector: 'app-create-event-button',
  templateUrl: './create-event-button.component.html',
  styleUrls: ['./create-event-button.component.css']
})
export class CreateEventButtonComponent {

  @Input() onClose: () => void;

  constructor(private modalService: ModalService) { }

  openCreateEventModal(): void {
    this.modalService.openModal<CreateEventPageComponent>(CreateEventPageComponent, {}, () => {
      if (this.onClose) {
        this.onClose();
      }
    });
  }
}



